const Extension = Autodesk.Viewing.Extension;

import { Generator } from "./Generator";

var av = Autodesk.Viewing;
var msdfNS = AutodeskNamespace('Autodesk.MSDF');
msdfNS["Generator"] = Generator;


/**
 * Computational geometry library extension
 */
class  MSDFExtension extends av.Extension {

    constructor(viewer, options) {
        super(viewer, options);
    }

    load() { return true; }
    unload() { return true; }
    activate() { return true; }
    deactivate() { return false; }
}

Autodesk.Viewing.theExtensionManager.registerExtension('Autodesk.MSDF', MSDFExtension);