import { MaxRectsPacker } from "maxrects-packer";

import * as opentype from "opentype.js";
import MSDFWorker from "worker-loader?inline!./GeneratorWorker";

const av = Autodesk.Viewing;

var Executer = function (tasks, workerNumber = 4) {
    // do this job with workers

    let workers = [];
    let pendingTasks = tasks.length;
    let taskId = 0;

    this.execute = function () {
        if (tasks.length == 0) {
            return Promise.resolve([]);
        }

        let results = [];
        let promise = new Promise((resolve, reject) => {
            for (let i = 0; i < workerNumber; i++) {
                let worker = new MSDFWorker();
                workers.push(worker);
                worker.onmessage = function (event) {

                    pendingTasks--;
                    results.push(event.data);

                    if (pendingTasks == 0) {
                        resolve(results);
                    }
                };
            }

            tasks.map((task) => {
                let index = taskId++ % workerNumber;
                workers[index].postMessage(task);
            })
        });
        return promise.then((results) => {
            this.finish();
            return results;
        }).catch(console.error);
    };

    this.finish = function () {
        for (let i = 0; i < workerNumber; i++) {
            workers[i].terminate();
        }
    }
};

/**
 * Control Point for generate msdf-font-atlas
 * 
 */

var Generator = function () {
    function locateChar(unicode, f1) {
        var chars = f1.data.toFontChar;
        var index = -1;
        for (var i = 0; i < chars.length; i++) {
            if (chars[i] == unicode) {
                index = i;
                break;
            }
        }
        if (f1.data.toUnicode._map) {
            return f1.data.toUnicode._map[index];
        }
    };

    var tasks = [];
    var outputData = {
        "pages": [],
        "chars": [],
        "charsMap": {},
        "info": [],
        "common": [],
        "distanceField": {},
        "kernings": [],
    };

    var infoIndex = 0;
    var commonIndex = 0;

    /**
      * 
      * @param {OpenType.Font} font 
      * @param {PDFFontObj} pdfFontObj 
      */
    this._prepare = function (font, pdfFontObj, option) {
        var charset = new Set();
        let fontSize = option.fontSize || 32;
        let range = option.range || 4;
        this.textureWidth = option.textureWidth || 1920;
        this.textureHeight = option.textureHeight || 1080;
        this.texturePadding = option.texturePadding || 4;

        let scale = fontSize / font.unitsPerEm;
        let currentInfoIndex = infoIndex++;
        let currentCommonIndex = commonIndex++;

        outputData.distanceField.fieldType = "msdf";
        outputData.distanceField.distanceRange = range;

        // Add font common info into the outputdata
        const os2 = font.tables.os2;
        const baseline = os2.sTypoAscender * (fontSize / font.unitsPerEm) + (range >> 1);
        let common = {
            "lineHeight": (os2.sTypoAscender - os2.sTypoDescender + os2.sTypoLineGap) * (fontSize / font.unitsPerEm),
            "base": baseline,
            "scaleW": this.textureWidth,
            "scaleH": this.textureHeight,
            "pages": 1,
            "packed": 0,
            "alphaChnl": 0,
            "redChnl": 0,
            "greenChnl": 0,
            "blueChnl": 0,
        };
        outputData.common.push(common);
        // var scaleSign = common. ? -1 : 1;

        let charectorSet = new Set();
        for (let key in font.glyphs.glyphs) {
            let glyph = font.glyphs.glyphs[key];
            var char = locateChar(glyph.unicode, pdfFontObj);
            var indexKey = `${pdfFontObj.data.loadedName}_${String.fromCharCode(glyph.unicode)}`;

            if (glyph.unicode != null && !charset.has(indexKey)) {
                var path = glyph.getPath(0, 0, fontSize, { xScale: scale, yScale: scale });
                var bounds = path.getBoundingBox();

                // if (char != '8') {
                //     continue;
                // }

                tasks.push({
                    fontName: pdfFontObj.data.name,
                    index: key,
                    path,
                    bounds,
                    range,
                    char,
                    fontSize,
                    unicodes: glyph.unicodes.slice(0),
                    info: currentInfoIndex,
                    common: currentCommonIndex,
                    advanceWidth: Math.round(glyph.advanceWidth * scale),
                    unicode: glyph.unicode
                });

                charset.add(indexKey);
                charectorSet.add(glyph.unicode);
                charectorSet.add(...glyph.unicodes);
            }
        }

        let info = {
            face: pdfFontObj.data.loadedName,
            fontname: pdfFontObj.data.name,
            size: fontSize,
            bold: 0,
            italic: 0,
            charset: Array.from(charectorSet).map((code) => String.fromCharCode(code)),
            unicode: 1,
            stretchH: 100,
            smooth: 1,
            aa: 1,
            padding: [0, 0, 0, 0],
            spacing: [0, 0],
            scale
        };
        outputData.info.push(info);
    };

    this._execute = function execute(cacheKey) {
        var executer = new Executer(tasks);
        return executer.execute().then((results) => {
            let packer = new MaxRectsPacker(this.textureWidth, this.textureHeight, this.texturePadding, {
                smart: true,
                pot: false,
                square: false,
                allowRotation: false,
                tag: false,
                border: 0
            });

            if (outputData.bins) {
                packer.load(outputData.bins);
            }

            if (results.length > 0) {
                packer.addArray(results);
                results.length = 0;
                let binIndex = 0;

                packer.bins.forEach(bin => {
                    var canvas = outputData.pages[binIndex];
                    if (canvas == null) {
                        const _document = this.getDocument();
                        var canvas = _document.createElement("canvas");
                        outputData.pages.push(canvas);

                        canvas.width = this.textureWidth;
                        canvas.height = this.textureHeight;

                        let ctx = canvas.getContext("2d");

                        ctx.save();
                        ctx.fillStyle = "#000000";
                        ctx.fillRect(0, 0, canvas.width, canvas.height);
                        ctx.restore();
                    }
                    canvas.cacheKey = new Date().getTime();

                    let ctx = canvas.getContext("2d");

                    bin.rects.forEach((item) => {
                        if (item.image == undefined) {
                            return;
                        }

                        var imageData = new ImageData(item.image.binData, item.image.width, item.image.height);

                        ctx.putImageData(imageData, item.x, item.y);
                        let charIndex = outputData.chars.length;
                        outputData.chars.push({
                            "index": item.index,
                            "aschar": item.char,
                            "width": item.width,
                            "height": item.height,
                            "inverseYAxis": item.inverseYAxis,
                            "xoffset": item.charInfo.xOffset,
                            "yoffset": item.charInfo.yOffset,
                            "txoffset": item.charInfo.txOffset,
                            "tyoffset": item.charInfo.tyOffset,
                            "xadvance": item.advanceWidth,
                            "chnl": 15,
                            "x": item.x,
                            "y": item.y,
                            "page": binIndex,
                            info: item.info,
                            common: item.common,
                            "unicode": item.unicode
                        })
                        item.unicodes.map((code) => {
                            if (!outputData.charsMap[item.fontName]) {
                                outputData.charsMap[item.fontName] = {};
                            }
                            outputData.charsMap[item.fontName][code] = charIndex;
                        })
                    });

                    // if(binIndex == 0) {
                    //     canvas.toBlob((blob) => {
                    //         let url = URL.createObjectURL(blob);
                    //         window.open(url);
                    //     }, "image/png");
                    // }
                    binIndex++;
                });
                let bins = packer.save();
                outputData.bins = bins;
                // console.log(JSON.stringify(outputData));
                this.saveCache(cacheKey, outputData);
                return outputData;
            }
            return outputData;
        });
    };

    this.getCacheKey = function (cacheKeyPrefix) {
        return `${cacheKeyPrefix}/pdf/fontAtlas/v0`;
    }

    this.saveCache = function (cacheKey, data) {
        // need to convert the pages in the data to base64/png data
        let pages = data.pages;
        data.pages = data.pages.map((canvas) => canvas.toDataURL("image/png"));
        Autodesk.Viewing.Private.LocalStorage.setItem(this.getCacheKey(cacheKey), JSON.stringify(data));

        // set the pages back
        data.pages = pages;
    };

    this.readCache = function (cacheKey) {
        // need to read pages base64/png data back to html canvas
        var data = Autodesk.Viewing.Private.LocalStorage.getItem(this.getCacheKey(cacheKey));
        if (data) {
            data = JSON.parse(data);
            if (data.pages && data.pages.length > 0) {
                let promises = [];
                for (var index = 0; index < data.pages.length; index++) {
                    let item = data.pages[index];
                    const _document = this.getDocument();
                    let canvas = _document.createElement("canvas");
                    canvas.width = data.common[index].scaleW;
                    canvas.height = data.common[index].scaleH;

                    let ctx = canvas.getContext("2d");
                    ctx.save();
                    ctx.clearRect(0, 0, canvas.width, canvas.height);

                    let image = new Image();
                    image.src = item;
                    promises.push(new Promise((resolve, reject) => {
                        image.onload = () => {
                            ctx.drawImage(image, 0, 0);
                            ctx.restore();
                            resolve();
                        };
                    }))
                    data.pages[index] = canvas;
                }

                return Promise.all(promises).then(() => {
                    return data;
                });
            } else {
                return Promise.resolve(null);
            }
        } else {
            return Promise.resolve(null);
        }
    };

    this.createFontAtlasForPDF = function (page, cacheKey, option) {
        let fontSize = option ? option.fontSize : 48;
        let range = option ? option.range : 4;
        // console.time("PDF load time FontAtlas");

        return this.readCache(cacheKey).then((data) => {
            // override current output data
            if (data) {
                outputData = data;
            }
        }).then(() => {
            return new Promise((resolve, reject) => {
                function needLoad(name) {
                    var needLoad = outputData.info.filter((item) => item.fontname === name).length == 0;
                    // if (needLoad) console.log("needLoad", name);
                    return needLoad;
                };

                for (var fontKey in page.commonObjs._objs) {
                    var f1 = page.commonObjs._objs[fontKey];
                    if (f1.data && f1.data.loadedName && f1.data.fallbackName && f1.data.data && needLoad(f1.data.name)) {
                        var font = opentype.parse(f1.data.data.buffer);
                        // font.download();
                        this._prepare(font, f1, { fontSize, range });
                    }
                }

                this._execute(cacheKey).then((fontAtlas) => {
                    // console.timeEnd("PDF load time FontAtlas");
                    resolve(fontAtlas);
                }).catch(console.error).then(resolve);
            });
        })
    };
};

av.GlobalManagerMixin.call(Generator.prototype);



export { Generator };



